<template>
  <LayoutPage class="page">
    <h1 class="page-title">Unit Conversion</h1>

    <div class="box">
      <div class="box__title">Setting Numeric</div>
      <div class="box__content">

        <div class="box__row">
          <div class="ir">
            <div class="ir__title">Change Number</div>
            <div class="ir__content">
              <div class="ans" v-if="nowUnit !== 'normal'">
                <div class="ans__number">{{ changeNumber }}</div>
                <div class="ans__unit">{{ nowUnit }}</div>
              </div>
            </div>
          </div>
        </div>

        <div class="box__row">
          <div class="ir">
            <div class="ir__title">Value</div>
            <div class="ir__content ir__content:input">
              <div class="input">
                <input type="text" id="px" class="input__entry" v-model.trim="nowPx">
                <label for="px" class="input__label">px</label>
              </div>
              <div class="input input--view" v-if="nowUnit !== 'normal'">
                <input type="text" id="view" class="input__entry" v-model.trim="otherVal">
                <label for="px" class="input__label">{{ nowUnitLabel }}</label>
              </div>
            </div>
          </div>
        </div>

        <div class="box__row">
          <div class="ir ir:pick">
            <div class="ir__title">Choose Unit</div>
            <div class="ir__content">
              <div class="option">
                <div class="option__item" v-for="(item, index) in options" :key="index">
                  <input type="radio" :id="`unit${item.val}`" name="unit" :value=item.val v-model="nowUnit">
                  <label :for="`unit${item.val}`">{{ item.val }}</label>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </LayoutPage>
</template>

<script setup>
import { defineOptions, ref, computed, watch } from 'vue'
import LayoutPage from '@/components/layout/page'

defineOptions({ name: 'unitConversion' })

const nowUnit = ref('rem');
const options = ref([
  {val: 'rem'},
  {val: 'em'},
  {val: '%'},
  {val: 'vw'},
  {val: 'vh'},
  {val: 'line-height'}
])

const nowPx = ref(16)
const otherVal = ref(16)

const filterUnit = (val) => {
  let label = ''
  let changeNumber = null
  switch (val) {
    case 'rem':
      label = 'Parent font size'
      changeNumber = nowPx.value / otherVal.value
      break;

    case 'em':
      label = 'Parent font size'
      changeNumber = nowPx.value / otherVal.value
      break;

    case '%':
      label = 'px'
      changeNumber = (nowPx.value / otherVal.value) * 100
      break;
    
    case 'vw':
      label = 'Browser Window Width'
      changeNumber = nowPx.value / (otherVal.value / 100)
      break;

    case 'vh':
      label = 'Browser Window Height'
      changeNumber = nowPx.value / (otherVal.value / 100)
      break;

    case 'line-height':
      label = 'Current font size'
      changeNumber = nowPx.value / otherVal.value
      break;
  }

  return { label, changeNumber }
}

const nowUnitLabel = computed(() => {
  let { label } = filterUnit(nowUnit.value)

  return label
})

const changeNumber = computed(() => {
  let { changeNumber } = filterUnit(nowUnit.value)

  return Number.isNaN(changeNumber) ? 0 : changeNumber
})

watch(nowUnit, (newUnit) => {
  switch (newUnit) {
    case 'rem':
    case 'em':
      if (nowPx.value === null || nowPx.value !== 16) nowPx.value = 16
      if (otherVal.value === null || otherVal.value !== 16) otherVal.value = 16
      break
    case '%':
    case 'vw':
      if (nowPx.value === null || nowPx.value !== 960) nowPx.value = 960
      if (otherVal.value === null || otherVal.value !== 1920) otherVal.value = 1920
      break
    case 'vh':
      if (nowPx.value === null || nowPx.value !== 540) nowPx.value = 540
      if (otherVal.value === null || otherVal.value !== 1080) otherVal.value = 1080
      break
    case 'line-height':
      if (nowPx.value === null || nowPx.value !== 16) nowPx.value = 24
      if (otherVal.value === null || otherVal.value !== 24) otherVal.value = 16
      break
  }
})
</script>

<style lang="sass" scoped>
@import @sass/init
@import @sass/helpers

@import ./style.sass
</style>